import {bootstrap, Component, Decorator, TemplateConfig, NgElement} from 'core/core';
import {NgRepeat} from 'directives/ng_repeat';
//import {firebase} from 'firebase';

@Component({
  selector: 'yo-app',
  template: new TemplateConfig({
    inline: `
      {{greeting}} <span red>world</span>!
      <my-repeat></my-repeat>
    `,
    directives: [RedDec, MyRepeat]
  })
})
class YoCmp {
  greeting: string;
  constructor() {
    this.greeting = 'Yo!';
  }
}

@Component({
  selector: 'my-repeat',
  template: new TemplateConfig({
    // without the wrapped div it's an error
    inline: `
    <div>
      <button (click)="updateItem()">Add</button>
      <input type="text" />
      <div template="ng-repeat #item in items">{{item}}</div>
    </div>`,
    directives: [NgRepeat]
  })
})
class MyRepeat {
  items: Array;
  el : NgElement;
  
  constructor(element: NgElement) {
    debugger;
    this.items = ['item1', 'item2'];
    this.el = element;
  }

  updateItem() {
    var addItem = this.el.domElement.shadowRoot.querySelector('input').value;
    this.items.push(addItem);
  }

}


@Decorator({
  selector: '[red]' // nothing to do with [] binding, its a css selector
})
class RedDec {
  constructor(el: NgElement) {
    el.domElement.style.color = 'red';
  }
}


// new equivalent to angular.module as far as starting a new app
export function main() {
  bootstrap(YoCmp);
}
